import { createRouter, createWebHashHistory } from "vue-router";

const router = createRouter({
  routes: [
    {
      path: "/",
      name: "public-welcome",
      meta: { title: "" },
      component: () => import("@mainapp/views/Welcome.vue")
    },
    {
      path: "/home",
      name: "auth-home",
      meta: { title: "" },
      redirect: "/home/history",
      component: () => import("@mainapp/views/home/Home.vue"),
      children: [
        {
          path: "history",
          name: "auth-history",
          meta: { title: "" },
          component: () => import("@mainapp/views/home/History.vue")
        },
        {
          path: "diagrams",
          name: "auth-diagrams",
          meta: { title: "" },
          component: () => import("@mainapp/views/home/Diagrams.vue")
        },
        {
          path: "shares",
          name: "auth-shares",
          meta: { title: "" },
          component: () => import("@mainapp/views/home/Shares.vue")
        },
        {
          path: "stars",
          name: "auth-stars",
          meta: { title: "" },
          component: () => import("@mainapp/views/home/Stars.vue")
        }
      ]
    },
    {
      path: "/entrance",
      name: "enter-entrance",
      meta: { title: "" },
      component: () => import("@mainapp/views/entrance/Entrance.vue")
    },
    {
      path: "/u",
      children: [
        {
          path: "profile/:uid",
          name: "public-profile",
          meta: { title: "" },
          component: () => import("@mainapp/views/user/Profile.vue")
        },
        {
          path: "setting",
          name: "auth-setting",
          meta: { title: "" },
          component: () => import("@mainapp/views/user/Setting.vue")
        }
      ]
    },
    {
      path: "/flowchat/:id",
      name: "auth-flowchat",
      meta: { title: "" },
      component: () => import("@mainapp/views/diagraming/FlowChat.vue")
    },
    {
      path: "/canvas/:id",
      name: "auth-canvas",
      meta: { title: "" },
      component: () => import("@mainapp/views/diagraming/Canvas.vue")
    },
    {
      path: "/mind/:id",
      name: "auth-mindmapping",
      meta: { title: "" },
      component: () => import("@mainapp/views/diagraming/MindMapping.vue")
    }
  ],
  history: createWebHashHistory(),
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { el: "#app", top: 0, behavior: "smooth" };
    }
  }
});

function isAuthenticated() {
  return !!localStorage.getStorageWithAge(KeyVals.MAINAPP_TOKEN_KEY);
}

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = ("BleuOn - " + to.meta.title) as string;
  }

  const name = to.name as string;
  const isAuth = isAuthenticated();

  if (name.startsWith("auth-") && !isAuth) {
    next("/entrance");
    ElMessage.error("");
  } else if (name.startsWith("public-")) {
    next();
  } else if (name.startsWith("enter-") && isAuth) {
    next("/home");
    ElMessage.warning("");
  } else {
    next();
  }
});

export default router;
